<template>
  <div class="fourohfour">
    <div id="bg"></div>
    <AppHeader></AppHeader>
    <div class="chart-component">
      <h1>404. Nothing Found.</h1>
      <router-link to="/">Back to Home</router-link>
      <svg id="fourohfourChart" width="700" viewBox="0 0 700 250" preserveAspectRatio style="width: 100%;" />
    </div>
  </div>
</template>

<script>
import AppHeader from './../AppHeader'
import * as d3 from 'd3'
export default {
  name: 'fourohfour',
  data () {
    return {
    }
  },
  components:{
    AppHeader
  },
  mounted(){
        //window.scrollTo(0, 0);
        document.title = '404. Not Found - Charts';

        var themeColor = '#000';

        var data = [{x: 0, y: 4}, {x: 50, y: 0}, {x:100, y:4}];
        var x = d3.scaleLinear()
            .domain([0, 100])
            .range([0, 620]);

        var y = d3.scaleLinear()
            .domain([0, 5])
            .range([200, 0]);

        var theChart = d3.select('#fourohfourChart')
            .append('g')
            .attr('id', 'theChart')
            .attr('transform','translate(20, 20)');

        theChart.append('g')
            .attr('id', 'x-axis')
            .attr('transform','translate(10, 210)')
            .call(d3.axisBottom(x)
            .ticks(0))
            //.tickSize(-700, 0 ,0)

        theChart.append('g')
            .attr('id', 'y-axis')
            .attr('transform','translate(0, 0)')
            .call(d3.axisLeft(y)
            //.tickSize(-620, 30 ,0)
            .ticks(3));
            

        var line = d3.line()
          .x(function(d) { return x(d.x); })
          .y(function(d) { return y(d.y); });

        theChart.append('path')
          .data([data])
          .attr('class', 'fourohfourline')
          .attr('d', line)
          .attr('transform','translate(10, 0)')
          .style('fill', 'none')

        d3.selectAll('text')
            .style('fill', themeColor);

        d3.selectAll('path, line')
            .style('stroke', themeColor);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  $card-width: 28vw;
  $small-screen: "only screen and (max-width : 600px)";
  $bg-color: #e0dbb3;
  $theme-color: #000;
  #bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $bg-color;
      z-index: -10000;
  }
  h1, p{
    color: $theme-color;
  }
  a{
    color: $theme-color;
  }
  header{
    background: none;
    border: 1px solid $theme-color;
    @media #{$small-screen}{
        border: none;
        border-bottom: 1px solid $theme-color;
    }
  }
</style>



